/* Style Yang digunakan untuk semua komponen */
* {
  margin: 0;
  padding: 0;
  color: #c9d1d9;
}
a {
  text-decoration: none;
}
body {
  background-color: #0d1117;
}
textarea {
  resize: none;
}
/* Header & Nav */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #161b22;
  margin-bottom: 20px;
  padding: 10px;
}
nav {
  color: #f0f6fc;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
nav a {
  color: wheats;
}
nav a:hover {
  color: white;
}
nav input {
  width: 200px;
  height: 25px;
  border-radius: 5px;
  border: 0.5px solid #30363d;
  color: white;
  background-color: #0d1117;
}
nav input:focus {
  outline: none;
  border: 0.5px solid #1b56af;
}
.navItem {
  padding: 0 20px;
}

/* Form tambah Catatan Baru */
.form {
  width: 45vw;
  padding: 5px 10px;
  background-color: #16171d;
  border: 0.5px solid #30363d;
  border-radius: 10px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
}
.formControl {
  margin: 5px;
  padding: 10px;
  width: 45vw;
  display: flex;
  flex-direction: column;
}
.formControl input,
textarea {
  padding: 0 5px;
  color: #ffffff;
  border: 0.5px solid #30363d;
  background-color: #0d1117;
  border-radius: 4px;
}
.formControl input:focus,
textarea:focus {
  outline: none;
  border: 0.5px solid #1b56af;
}
.formControl input {
  height: 30px;
}
.formControl p {
  font-size: 12px;
  text-align: end;
}
.formControl button {
  height: 35px;
  background-color: #16171d;
  border: 2px solid #1f6feb;
  border-radius: 5px;
  transition: 0.3s;
}
.formControl button:hover {
  background-color: #1f6feb;
}
.form #close {
  background-color: #16171d;
}

/* Catatan */
.noteApp {
  width: 80vw;
  margin: auto;
}
.notes {
  padding: 10px;
  min-height: 200px;
  display: flex;
  flex-wrap: wrap;
}
.noteList {
  height: 300px;
  width: 300px;
  margin: 10px;
  border-radius: 5px;
  border: 1px solid #30363d;
}
.noteTitle {
  flex-direction: column;
  height: 50px;
  padding: 0 10px;
  border-radius: 5px;
  background-color: #161b22;
  border-bottom: 1px solid #1d2228;
}
.noteTitle h3 {
  height: 25px;
  overflow: hidden;
}
.noteTitle p {
  color: gray;
  text-align: end;
  font-size: 12px;
}
.noteBody {
  padding: 20px 10px 10px 10px;
  text-align: justify;
  height: 150px;
  overflow: auto;
}
.noteButton {
  margin: 1px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.noteButton a {
  padding-top: 1px;
  width: 100%;
  height: 30px;
  text-align: center;
  transition: 0.3s;
}
.delete {
  border: 0.3px solid red;
  color: white;
  margin-bottom: 3px;
}
.delete:hover {
  background-color: red;
}
.archive {
  border: 0.3px solid rgb(1, 80, 1);
  color: white;
}
.archive:hover {
  background-color: rgb(1, 80, 1);
}
.emptyState {
  margin: auto;
  color: grey;
}
/* Media Query Tablet */
@media screen and (max-width: 768px) {
  .form {
    width: 70vw;
  }

  nav input {
    width: 150px;
  }
  .formControl {
    margin: 5px;
    padding: 10px;
    width: 70vw;
    display: flex;
    flex-direction: column;
  }
} /* Media Query Mobile */
@media screen and (max-width: 576px) {
  header {
    flex-direction: column;
  }
  nav {
    margin-top: 10px;
  }
  .form {
    width: 80vw;
  }
  .formControl {
    margin: 5px;
    padding: 10px;
    width: 80vw;
    display: flex;
    flex-direction: column;
  }
  .notes {
    justify-content: center;
  }

  nav input {
    width: 35vw;
  }
  .noteList {
    width: 100vw;
  }
}
